export interface MenuItem {
  label: string;
  name?: string;
  link?: string;
  target?: string;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  routerLink?: any;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  route?: any;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  action?: (event?: any, id?: string) => void;
  /**
   * If set to true, will exclude adding active class to the navigation item of type action
   * @since v12.9.0
   */
  excludeActiveOnAction?: boolean;
  closeOnClick?: boolean;
  id: string;
  active?: boolean;
  selected?: boolean;
  hide?: boolean;
  className?: string;     // add class to menu item (a or button)
  listClassName?: string; // add custom class(s) to the item (li tag)
  items?: MenuItem[];
  subMenu?: MenuItem[];
  header?: string;
  expanded?: boolean;
  disabled?: boolean;
  separator?: boolean;
  parent?: MenuItem;
  filteringConfig?: MenuItemFilteringConfig;
  testID?: string;
}

export interface MenuItemFilteringConfig {
  enabled: boolean;
  placeholder?: string;
  resultsTitle?: string;
  title?: string;
  notFoundText?: string;
  btnClearText?: string;
}

export interface CascadeItem {
  id: string;
  subMenuItems?: {
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    parentNode: any;
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    componentRef: any;
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    anchor: any;
  }[];
}
